import React from 'react';
import ReactDOM from 'react-dom';
import { App } from './components/App';
import { mergeStyles } from '@uifabric/styling';
import { FluentCustomizations } from '@uifabric/fluent-theme';
import { Customizer, PrimaryButton, IButtonProps } from 'office-ui-fabric-react';

declare interface ReactFabric {
    reactRenderApp: Function; //just a test to see if the default React app would display... it does!

    primaryButton: Function;  
}

declare global {
    interface Window {
        reactFabric: ReactFabric;
    }
}



// Inject some global styles
mergeStyles({
  selectors: {
    ':global(body), :global(html), :global(#app)': {
      margin: 0,
      padding: 0,
      height: '100vh'
    }
  }
});

window.reactFabric = {

    reactRenderApp: function () {
        ReactDOM.render(
            <Customizer {...FluentCustomizations}>
                <App />
            </Customizer>,
            document.getElementById('app')
        );
    },
    primaryButton: function (elementRef: HTMLElement, buttonProps: IButtonProps) {
        ReactDOM.render(
            <Customizer {...FluentCustomizations}>
                <PrimaryButton {...buttonProps}   />
            </Customizer>,
            elementRef
        );
    }

}
